.footer__wraper {
@include tablet {
        display: flex;
        justify-content: space-between;
        margin-bottom: 32px;
    }
@include desktop {
        margin-bottom: 60px;
    }
}

.footer__wraper--auto {
    margin-top: auto;
    margin-bottom: auto;
}

.footer__text {
    margin-bottom: 16px;
    padding: 0 2px;
    font-weight: 700;
    font-size: 18px;
    line-height: calc(22 / 18);
    text-transform: uppercase;
    color: $inverse-color;

    @media screen and (max-width: 767px) {
        max-width: 232px;
    }

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 8px;
        max-width: 352px;
        font-size: 26px;
        line-height: calc(32 / 26);
        letter-spacing: 0.04em;
    }

    @include desktop {
        margin-bottom: 14px;
        font-weight: 400;
        font-size: 30px;
        line-height: calc(37 / 30);
        letter-spacing: 0.04em;
    }
}

.footer__strong {
    display: block;
    margin-bottom: 60px;
    font-weight: 700;
    font-size: 34px;
    line-height: calc(41 / 34);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $inverse-color;

    @media screen and (max-width: 767px) {
        max-width: 200px;
    }

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 0;
        max-width: 352px;
        font-size: 48px;
        line-height: calc(59 / 48);
        letter-spacing: 0.06em;
    }

    @include desktop {
        margin-bottom: 0;
        font-size: 58px;
        line-height: calc(71 / 58);
        letter-spacing: 0.06em;
    }
}

.socials {
    display: flex;
    justify-content: flex-end;
    gap: 20px;
    margin-bottom: 20px;

    @include tablet {
        margin-bottom: 34px;
    }

    @include desktop {
        margin-bottom: 40px;
    }
}

.socials__icon {
    fill: $bg-color-body;
    @include transition (fill);

    &:hover,
    &:focus {
        fill: $inverse-color;
    }
}

.footer__text-link {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 12px;
    line-height: calc(22 / 12);
    text-align: right;
    color: $primary-text-color;

    @include tablet {
        margin-bottom: 0;
    }
}

.footer__link {
    color: $inverse-color;
}

.footer__line {
    margin: 0 -20px 20px -20px;
    size: 1px;
    width: calc(100% + 40px);
    color: #e1e1e1;

    @include tablet {
        margin: 0 -32px 12px -32px;
        width: calc(100% + 64px);
    }

    @include desktop {
        margin: 0 0 12px;
        width: 100%;
    }
}

.footer__copyright {
    padding-bottom: 20px;
    font-weight: 700;
    font-size: 14px;
    line-height: calc(21 / 14);
    text-align: center;
    color: $copyright-color;

    @media screen and (max-width: 767px) {
        margin-right: auto;
        margin-left: auto;
        width: 155px;
    }

    @include tablet {
        padding-bottom: 12px;
        line-height: calc(26 / 14);
        letter-spacing: 0.02em;
    }

    @include desktop {
        font-size: 12px;
        line-height: calc(22 / 12);
        letter-spacing: 0.02em;
    }
}